<template>
  <div class="app_head_box">
    <div class="login_box_img">
      <div class="login_pop_box">
        <div class="login_head_img">
          <img src="../../static/images/login/头像图标.png">
        </div>
        <div class="login_name ">
          <input type="text" placeholder="帐号" v-model="resAccount">
        </div>
        <div class="login_pwd">
          <input type="password" placeholder="密码" v-model="resAccPwd">
        </div>
        <div class="login_but" @click="loginClick">登录</div>
      </div>
    </div>
  </div>
</template>

<script>
import { car } from '../port/ports'
export default {
  name: 'login',
  data () {
    return {
      resAccount:'',
      resAccPwd:'',
      personnelCompanyId:'',
    }
  },
  created(){
    
  },
  mounted(){
    window.scrollTo(0,0);
  },
   methods:{
    loginClick(){
      if(this.verifyAdminLogin()){
        this.resLoginClick();
      }
    },
    //登陆
    resLoginClick(){
      let body = {
        username:this.resAccount,
        password:this.resAccPwd,
      }
      let self = this;
      this.post(car.login,body,function(res){
        // console.log(res);
        if(res.data.code == 200){
          self.$message("登录成功！");
          let userName = res.data.data.sysUsername;
          sessionStorage.setItem("userName",userName);
          self.$router.push({path:"./index"});
        }else{
          self.$message(res.data.msg);
        }
      });
    },
    //检查登录是否为空
    verifyAdminLogin(){
      if(!this.resAccount){
        alert('请输入您的帐号！');
        return false;
      }
      if(!this.resAccPwd){
        alert('请输入您的密码！');
        return false;
      }
      return true;
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login_box_img{
  width: 100%;
  background:url('../../static/images/login/底部banner.png');
  height: 800px;
  border:1px solid #fff;
}
.login_pop_box{ 
  width: 460px;
  height: 439px;
  margin: auto; 
  margin-top:150px; 
  background-color: #fff;
  box-shadow: 0px 3px 24px 0px 
  rgba(0, 0, 0, 0.35);
  border-radius: 10px;
}
.login_pop_box input {
  height: 38px;
  width: 360px;
  outline: none;
  margin: 10px;
  padding-left: 15px;
  border: 1px solid #eee;
  border-radius: 4px;
  background: #eee;
}
.login_head_img{
  height: 100px;
  padding-top: 50px;
}
.login_but{
  margin: 50px 50px;
  height: 40px;
  width: 360px;
  font-size: 18px;
  line-height: 40px;
  background:url(../../static/images/login/登录按钮.png);
}
</style>


