<template>
  <div class="admin_box">
    <div class="admin_left">
      <h2>标远科技 车贴后台管理系统</h2>
      <div class="admin_img">
        <div class="login_box">
          <p>登录名：{{userName}}</p>
        </div>
      </div>
      <div class="admin_but">
        <el-button type="primary" round @click="exitLogin">退出登录</el-button>
      </div>
    </div>
    <div class="admin_right">
      <el-form :inline="true" :model="userList" class="demo-form-inline">
        <el-form-item label="用户姓名:">
          <el-input placeholder="请输入用户姓名" clearable v-model="userList.name"></el-input>
        </el-form-item>
        <el-form-item label="联系电话:">
          <el-input placeholder="请输入联系电话" clearable v-model="userList.phone"></el-input>
        </el-form-item>
        <el-form-item label="是否绑定:">
          <el-select v-model="userList.bind" clearable filterable placeholder="请选择" ref ="selId">
            <el-option
              v-for="item in bindList"
              :key="item.label"
              :label="item.value"
              :value="item.label">
            </el-option>
          </el-select>
        </el-form-item>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="getData">查询</el-button>
        </el-form-item>
      </el-form>  
      <el-table :data="tabledata.data" border>
        <template v-for="(value,key,index) in tabledata.list" v-if="value!='微信头像'">
          <el-table-column :prop="key" :label="value"  :key="index" :formatter="changeData" :show-overflow-tooltip="true">
          </el-table-column>
        </template>
        <template v-else>
          <el-table-column label="微信头像">
            <template slot-scope="scope">
              <div class="user_img">
                <img :src="scope.row.userWxImg">
              </div>
          </template>
          </el-table-column>
        </template>
      </el-table>
      <div class="table-foot page_right">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page.sync="pageObj.targetPage"
          :page-size="pageObj.pageSize"
          layout="total, prev, pager, next"
          :total="pageObj.recordCount">
        </el-pagination> 
      </div>
    </div>
  </div>
</template>

<script>
import { car } from '../port/ports'
export default {
  name: 'index',
  data () {
    return {
      tabledata:{
        list:{
          userWxImg:'微信头像',
          userWxName:'微信昵称',
          userName:'用户姓名',
          userQrCode:'绑定码',
          userTelphone:'联系电话',
          userCarCode:'车牌号',
          userIsBind:'是否绑定',
        },
        data:[]
      },
      userName:'',
      pageObj:{
        targetPage:1,
        pageSize:8,
      },
      bindList:[
        {label:0,value:'未绑定'},
        {label:1,value:'已绑定'},
      ],
      userList:{
        name:'',
        phone:'',
        bind:'',
      },
    }
  },
  created(){
    this.userName = sessionStorage.getItem("userName");
    this.getData();
  },
  mounted(){

  },
  methods:{
    getData:function(){
      var body ={
        page:this.pageObj.targetPage,
        size:this.pageObj.pageSize,
        username:this.userList.name?this.userList.name:{},
        phone:this.userList.phone?this.userList.phone:{},
        isBind:this.userList.bind || this.userList.bind == 0 ?this.userList.bind:{},
      }
      var self = this;
      this.post(car.userList,body,function(res){
         var res = res.data;
         // console.log(res);
       if(res.code == 200){
         self.tabledata.data = res.data.userList;
         self.pageObj = res.data.queryPage;
       }else{
         self.$message(res.msg);
       }
      },function(error){

      })
    },
    changeData:function(row,column){
      switch(column.property){
        case 'userIsBind':
          return this.isBind(row.userIsBind);
          break;
        default:
        return row[column.property];
        break;
      }
    },
    exitLogin(){
      let self = this;
      this.post(car.logout,'',function(res){
        // console.log(res);
        if(res.data.code == 200){
          self.$message("退出当前帐号成功！");
          self.$router.push({path:"./"});
        }else{
          self.$message(res.data.msg);
        }
      });
    },
    isBind(statue){
      switch(statue){
        case 0:return "未绑定";
        case 1:return "已绑定";
      }
    },
    handleCurrentChange(page){
      this.pageObj.targetPage = page;
      this.getData();
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.admin_box{
  position: absolute;
  width: 100%;
  min-width: 1200px;
  min-height: 100%;
  background: url('../../static/images/背景.jpg') no-repeat;
  background-size: 100% 100%;
}
.admin_left{
  position: absolute;
  width: 25%;
  height: 100%;
}
.admin_left h2{
  margin: 15% auto;
}
.admin_img{
  position: absolute;
  width: 80%;
  height: 60%;
  left: 10%;
  background: url('../../static/images/touxian.jpg') no-repeat;
  background-size: 100% 100%;
}
.admin_but{
  position: absolute;
  width: 100%;
  bottom: 13%;
}
.login_box{
  width: 100%;
  position:absolute;
  bottom: 6%;
}
.admin_right{
  width: 60%;
  position:absolute;
  top: 10%;
  left: 30%;
}
.page_right{
  text-align: right;
  margin: 30px 0;
}
.user_img{
  width: 35px;
  height: 35px;
  border-radius: 10px;
  margin: auto;
}
.user_img img{
  width: 100%;
  border-radius: 10px;
}
</style>


