import qs from 'qs'
export default{
  data () {
    return {
      
    }
  },
	methods: {
    post:function(URL,body,callback,errorback){//goback为调用此方法的方法
      this.$axios({
      	method:'post',
	      url:URL, // 请求路径
	      timeout:'3000',
	      headers: {
	      	'Content-Type':'application/x-www-form-urlencoded',
          // 'Content-Type':'application/json',
      },
	      data:qs.stringify(body),//请求参数
        // datatype: 'form',
	      withCredentials:true,   //允许跨域携带cookit

	  })

        .then(callback)//成功回调
        .catch(errorback); //失败回调
    },
    get:function(URL,parameters,callback,errorback){//goback为调用此方法的方法
      if(parameters != null && parameters != 'undefined'){
        URL += qs.stringify(parameters);
      }
      // console.log(URL);
      this.$axios({
        method:'get',
        url:URL, // 请求路径
        timeout:'3000',
        headers: {
          'Content-Type':'application/x-www-form-urlencoded',
          // 'Content-Type':'application/json',
        }
      })
      .then(callback) //成功回调
      .catch(errorback); //失败回调
    },
  }
}


